import React from 'react'; 

const TableHead  =  () => {

    return(
        <thead>
            <tr>
            <th scope="col">Date</th>
            <th scope="col">Temperature (ºC)</th>
            <th scope="col">Temperature (ºF)</th>
            <th scope="col">Action</th>
            </tr>
        
           
        </thead>
    
     
       

    );
}

export default TableHead;